@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: 0;
  font-family: 'Comic Neue', cursive;
  background-color: black;
}
.MainPage {
  color: white;
}

.MainPage h1 {
  font-size: 60px;
  font-weight: 600;
  margin-top: 30px;
  text-align: center;
  text-shadow: 0 5px 10px red;
}

.first-container {
  align-items: center;
  display: flex;
  justify-content: space-around;
}

.second-container {
  align-items: center;
  display: flex;
  justify-content: space-around;
}

.image {
  width: 20%;
}

.jason {
  object-fit: contain;
  width: 300px;
}

.MainPage button {
  background-color: gray;
  border-radius: 15px;
  box-shadow: 0 0 18px red;
  color: rgb(179, 0, 0);
  cursor: pointer;
  font-weight: 600;
  height: 5%;
  padding: 0.9%;
  width: 10%;
}

hr {
  border: none;
  box-shadow: 0 5px 10px red;
  margin-bottom: 30px;
  padding: 1px;
}

.Card {
  width: 15em;
  height: 30em;
  flex-basis: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
  color: white;
  text-decoration: none;
  border: none;
}

.Card img {
  width: 15em;
  box-shadow: 0 0 14px white;
  z-index: 1;
  cursor: pointer;
}

.Card h3 {
  width: 12em;
  text-align: center;
  margin-top: 10px;
  text-shadow: 0 5px 10px red;
  margin-bottom: 10px;
}

.Card span {
  border: 1px solid white;
  color: yellow;
  padding: 10px;
  border-radius: 10px;
  font-weight: 600;
  background-color: black;
}
.title-button {
  border: none;
  outline: none;
  background-color:rgba(66, 65, 65, 0.9);
  font-size: 30px;
  text-shadow: 0 5px 23px red;
  color: white;
  cursor: pointer;
  font-weight: 600;
}

.Header {
  display: flex;
  justify-content: space-around;
  height: 60px;
  background-color:rgba(66, 64, 65, 0.9);
  align-items: center;
  font-size: 30px;
  text-shadow: 0 5px 10px red;
}

.search-container {
  display: flex;
  background-color:rgba(66, 65, 65, 0.9);
  align-items: center;
}

.search-container input, .search-container button {
    background-color: white;
    border-radius: 15px;
    height: 30px;
    outline: none;
    box-shadow: 0 0 18px red;
    margin-left: 10px;
}

.search-container button {
  width: 80px;
  align-items: center;
}

.Movies {
  color: white;
  display: flex;
  flex-direction: column;
}

.Movies h1 {
  text-align: center;
  margin: 30px 0;
}

.movie-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.category-buttons {
  color: white;
  height: 30px;
  flex-basis: 16%;
  width: 100px;
  text-shadow: 0 5px 10px red;
  font-size: 30px;
  border: none;
  margin-bottom: 10px;
  cursor: pointer;
  font-weight: 600;
}

.category-buttons:hover {
  color: red;
  text-shadow: 0 2px 6px white;
  height: 30px;
  flex-basis: 16%;
  width: 100px; 
  font-size: 30px;
  border: none;
  margin-bottom: 10px;
  cursor: pointer;
}

.genre-container {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-bottom: 50px;
  border: 1px solid white;
  width: 90%;
  align-self: center;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 0 14px red;
  border: 1px solid rgb(172, 0, 0);
}
.Details {
  color: white;
}

.info {
  display: flex;
  justify-content: space-between;
}

.info-movie {
  margin: 1% 0 0 3%;
  text-shadow: 0 5px 10px red;
}

.info span {
  margin: 1% 3%;
  padding: 10px;
  border-radius: 15px;
  background-color: yellow;
  color: black;
  font-weight: 600;
  box-shadow: 0 0 20px red;
}

.overview {
  display: flex;
  width: 80%;
  justify-content: space-between;
  margin: 3% 0 3% 10%;
  align-items: center;
  box-shadow: 0 0 14px red;
}

.overview img {
  width: 20%;
}

.overview p {
  width: 70%;
}

.review {
  text-align: center;
  margin-bottom: 30px;
  text-shadow: 0 5px 10px red;
}

.overview-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 400px;
  justify-content: space-evenly;
}

.overview-container h1 {
  text-shadow: 0 5px 10px red;
  margin-bottom: 30px;
  margin-top: 40px;
}

.back {
  color: white;
  height: 30px;
  width: 100px;
  text-shadow: 0 5px 10px red;
  font-size: 30px;
  border: none;
  margin-top: 1%;
  cursor: pointer;
  font-weight: 600;
}

.back:hover {
  color: red;
  text-shadow: 0 2px 6px white;
  height: 30px;
  width: 100px; 
  font-size: 30px;
  border: none;
  cursor: pointer;
}

.genres-container {
  display: flex;
  justify-content: space-evenly;
  width: 30vw;
}

.genre {
  padding: 10px;
  border-radius: 15px;
  font-weight: 600;
  box-shadow: 0 0 14px red;
  border: 1px solid rgb(172, 0, 0);
  margin-left: 15px;
}

.paragraph-overview {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.trailer-container {
  height: 600px;
  margin-bottom: 50px;
  display: flex;
  justify-content: center;
}

.video {
  width: 60%;
  box-shadow: 0 0 14px red;
  border: 1px solid rgb(172, 0, 0);
  height: 100%;
  align-self: center;
}
.author {
  display: flex;
  align-items: center;
  justify-content: left;
  margin-bottom: 20px;
  margin-left: 6%;
}

.author img {
  width: 5%;
  border-radius: 100px;
  margin-right: 20px;
}

.content {
  display: flex;
  width: 90%;
  margin-bottom: 50px;
  margin-left: 5%;
}

